/* Основные стили */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	background-color: #f5f7fa;
	color: #333;
	line-height: 1.6;
}

.App {
	display: flex;
	min-height: 100vh;
	padding: 20px;
	gap: 30px;
	max-width: 1600px;
	margin: 0 auto;
}

/* Заголовок */
.title {
	position: absolute;
	top: 20px;
	left: 50%;
	transform: translateX(-50%);
	font-size: 2rem;
	font-weight: 700;
	color: #2c3e50;
	text-align: center;
	margin-bottom: 30px;
	padding-bottom: 10px;
	width: fit-content;
}

/* Сайдбар с фильтрами */
.filters-sidebar-form {
	width: 320px;
	background: white;
	padding: 25px;
	border-radius: 12px;
	box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
	height: fit-content;
	top: 20px;
	border: 1px solid #e1e5eb;
}

.filters-sidebar-form h2 {
	color: #2c3e50;
	margin-bottom: 25px;
	font-size: 1.4rem;
	text-align: center;
	padding-bottom: 15px;
	border-bottom: 2px solid #f0f2f5;
}

/* Поля формы */
.form-field {
	margin-bottom: 18px;
}

.form-field label {
	display: block;
	margin-bottom: 6px;
	font-weight: 600;
	color: #4a5568;
	font-size: 0.9rem;
}

.form-field input {
	width: 100%;
	padding: 10px 14px;
	border: 1.5px solid #e2e8f0;
	border-radius: 8px;
	font-size: 0.95rem;
	transition: all 0.3s ease;
	background-color: #f8fafc;
}

.form-field input:focus {
	outline: none;
	border-color: #3498db;
	box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
	background-color: white;
}

.form-field input:hover {
	border-color: #cbd5e0;
}

/* Кнопка применения формы */
.filters-sidebar-form button {
	width: 100%;
	padding: 12px;
	background: #13467c;
	color: white;
	border: none;
	border-radius: 8px;
	font-size: 1rem;
	font-weight: 600;
	cursor: pointer;
	transition: all 0.3s ease;
	margin-top: 10px;
	letter-spacing: 0.5px;
}

.filters-sidebar-form button:hover {
	background: #13467c;
	transform: translateY(-2px);
	box-shadow: 0 4px 12px rgba(52, 152, 219, 0.3);
}

.filters-sidebar-form button:active {
	transform: translateY(0);
}

/* Основное содержание с таблицей */
.content-area {
	display: flex;
	gap: 10px;
	flex: 1;
	padding-top: 60px;
}

/* Обертка таблицы */
.table-container {
	width: 100%;
	max-width: 1400px;
	margin: 0 auto;
	overflow-x: auto;
	border-radius: 12px;
	box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
	background: white;
	border: 1px solid #e1e5eb;
}

/* Стили таблицы */
.users-table {
	width: 100%;
	border-collapse: collapse;
	min-width: 1200px;
}

.users-table thead {
	background: #13467c;
	color: white;
}
.header-row th{
	transition: background-color 0.2s ease;
	
}
.header-row th:hover{
	cursor: pointer;
	background: #175496;
}
.unclickable{
	cursor: default !important;
	background: fixed !important;
}

.users-table th {
	padding: 18px 15px;
	text-align: left;
	font-weight: 600;
	font-size: 0.95rem;
	/* border-right: 1px solid rgba(255, 255, 255, 0.1); */ /*Может потом убрать?*/
	position: relative;
	text-align: center;
}

.users-table th:last-child {
	border-right: none;
}

.users-table th:after {
	content: '';
	position: absolute;
	right: 0;
	top: 20%;
	height: 60%;
	width: 1px;
	background: rgba(255, 255, 255, 0.1);
}

.users-table th:last-child:after {
	display: none;
}

.users-table tbody tr {
	border-bottom: 1px solid #f1f5f9;
	transition: background-color 0.2s ease;
}

.users-table tbody tr:hover {
	background-color: #0d82ff3a;
}

.users-table td {
	padding: 16px 15px;
	color: #4a5568;
	font-size: 0.9rem;
	border-right: 1px solid #f1f5f9;
	text-align: center;
}

.users-table td:last-child {
	border-right: none;
}

/* Состояния загрузки и ошибки */
.loading-state,
.error-state {
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 400px;
	font-size: 1.2rem;
	color: #4a5568;
	background: white;
	border-radius: 12px;
	box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
	max-width: 1400px;
	margin: 0 auto;
}

.loading-state {
	color: #3498db;
}

.error-state {
	color: #e74c3c;
	padding: 40px;
}

/* Модальное окно пользователя */
.user-info-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.user-info {
  background: white;
  border-radius: 12px;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  border: 1px solid #e1e5eb;
  position: relative;
  animation: slideUp 0.3s ease;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Кнопка закрытия */
.close-wrap {
  position: sticky;
  top: 0;
  background: white;
  border-bottom: 1px solid #f1f5f9;
  padding: 15px 25px;
  display: flex;
  justify-content: flex-end;
  z-index: 10;
}

.close-symbol {
  font-size: 1.5rem;
  color: #4a5568;
  cursor: pointer;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.close-symbol:hover {
  background-color: #f8fafc;
  color: #2c3e50;
  transform: scale(1.1);
}

.close-symbol:active {
  transform: scale(0.95);
}

/* Аватар */
.avatar-wrap {
  display: flex;
  justify-content: center;
  padding: 20px 25px 10px;
}

.avatar-wrap img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #f0f2f5;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Область с информацией */
.info-area {
  padding: 20px 25px 30px;
}

.info-area p {
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #f1f5f9;
  color: #4a5568;
  font-size: 0.95rem;
  line-height: 1.5;
}

.info-area p:last-child {
  margin-bottom: 0;
  border-bottom: none;
}

/* .info-area p:nth-child(odd) {
  background-color: #f8fafc;
  padding: 12px 15px;
  border-radius: 6px;
  margin-left: -15px;
  margin-right: -15px;
} */

.info-area p strong {
  color: #2c3e50;
  font-weight: 600;
  min-width: 140px;
  display: inline-block;
}

/* Состояния загрузки и ошибки внутри модалки */
.user-info .loading-state,
.user-info .error-state {
  min-height: 200px;
  padding: 40px 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.user-info .loading-state {
  color: #3498db;
  font-size: 1.1rem;
}

.user-info .error-state {
  color: #e74c3c;
  font-size: 1.1rem;
}

/* Адаптивность */
@media (max-width: 768px) {
  .user-info {
    max-width: 95%;
    max-height: 85vh;
  }
  
  .avatar-wrap img {
    width: 100px;
    height: 100px;
  }
  
  .info-area {
    padding: 15px 20px 25px;
  }
  
  .info-area p {
    font-size: 0.9rem;
    margin-bottom: 10px;
    padding-bottom: 10px;
  }
  
  .close-wrap {
    padding: 12px 20px;
  }
}

@media (max-width: 480px) {
  .user-info-modal-overlay {
    padding: 10px;
  }
  
  .user-info {
    max-width: 100%;
    max-height: 90vh;
  }
  
  .avatar-wrap {
    padding: 15px 20px 5px;
  }
  
  .avatar-wrap img {
    width: 80px;
    height: 80px;
  }
}

@media (max-width: 1200px) {
	.App {
		flex-direction: column;
		padding: 15px;
	}

	.filters-sidebar-form {
		width: 100%;
		position: static;
		margin-bottom: 20px;
	}

	.content-area {
		padding-top: 0;
	}
}

@media (max-width: 768px) {
	.title {
		font-size: 1.6rem;
		top: 15px;
	}

	.filters-sidebar-form {
		padding: 20px;
	}

	.table-container {
		border-radius: 8px;
	}

	.users-table th,
	.users-table td {
		padding: 12px 10px;
		font-size: 0.85rem;
	}
}

/* Анимация загрузки */
@keyframes pulse {

	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}
}

.loading-state {
	animation: pulse 1.5s infinite;
}